const comparisonWrapper = document.querySelector('.comparison__wrapper');
const comparisonHeader = comparisonWrapper.querySelector('.comparison__tab-header');
const comparisonHeaderButtons = comparisonHeader.querySelectorAll('.comparison-tab__button');
const tabTitle = comparisonWrapper.querySelector('.comparison__header');
const tabBody = comparisonWrapper.querySelector('.comparison__body');
let activeTab = 'title';

comparisonHeaderButtons.forEach(button => {
    button.addEventListener('click', () => {
        comparisonHeaderButtons.forEach(button => button.classList.remove('active'));
        button.classList.add('active');
        if (button.classList.contains('comparison-tab__header-button')) {
            activeTab = 'title';
        } else {
            activeTab = 'body';
        }
        switchTab(activeTab);
    })
});

function switchTab(tab) {
    if (tab === 'title') {
        tabTitle.classList.add('active');
        tabBody.classList.remove('active');
    } else {
        tabTitle.classList.remove('active');
        tabBody.classList.add('active');
    }
}
